<template lang="pug">
  el-collapse
    el-collapse-item(:title="$t('calculator')" name="1")
      el-row.trauma-timeline-ages-col(:span="24")
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="firstValueYears", type="number", clearable, min="0", :placeholder="$t('years')", @input="checkAgeYears('FIRST_VALUE_YEARS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
        el-col(:span="2") &nbsp
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="firstValueMonths", type="number", clearable, min="0", max="11", :placeholder="$t('monthsInput')", @input="checkAgeMonths('FIRST_VALUE_MONTHS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-row &nbsp
      el-row.trauma-timeline-ages-col(:span="24")
        el-col(:span="4") &nbsp
        el-col(:span="4")
          el-button(:type="divideSelected" @click="selectOperator('DIVIDE')")
            i(class="fas fa-divide")
        el-col(:span="4")
          el-button(:type="multiplySelected" @click="selectOperator('MULTIPLY')")
            i(class="fas fa-times")
        el-col(:span="4")
          el-button(:type="plusSelected" @click="selectOperator('PLUS')")
            i(class="fas fa-plus")
        el-col(:span="4")
          el-button(:type="minusSelected" @click="selectOperator('MINUS')")
            i(class="fas fa-minus")
        el-col(:span="4") &nbsp
      el-row &nbsp
      el-row.trauma-timeline-ages-col(v-if="ageValue" :span="24")
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="secondValueYears", type="number", clearable, min="0", :placeholder="$t('years')", @input="checkAgeYears('SECOND_VALUE_YEARS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
        el-col(:span="2") &nbsp
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="secondValueMonths", type="number", clearable, min="0", max="11", :placeholder="$t('monthsInput')", @input="checkAgeMonths('SECOND_VALUE_MONTHS')" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-row.trauma-timeline-ages-col(v-if="numberValue" :span="24")
        el-input.trauma-timeline-ages-input(v-model="orderValue", type="number", clearable, min="1", :placeholder="$t('value')", @input="checkOrderValue()" onkeydown="javascript: return event.keyCode == 69 ? false : true")
      el-row &nbsp
      el-row
        el-button(@click="calculate")
          i(class="fas fa-equals")
      el-row &nbsp
      el-row.trauma-timeline-ages-col(:span="24")
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="yearResult", disabled, type="number", :placeholder="$t('years')")
        el-col(:span="2") &nbsp
        el-col(:span="11")
          el-input.trauma-timeline-ages-input(v-model="monthResult", disabled, type="number", :placeholder="$t('monthsInput')")
</template>

<script>
import timeMachine from '@/utils/time-machine'

export default {
  name: 'AgeCalculator',
  data() {
    return {
      yearResult: '',
      monthResult: '',
      firstValueYears: '',
      firstValueMonths: '',
      secondValueYears: '',
      secondValueMonths: '',
      orderValue: '',
      operator: 'DIVIDE',
      divideSelected: 'primary',
      multiplySelected: '',
      plusSelected: '',
      minusSelected: '',
      numberValue: true,
      ageValue: false,
    }
  },
  computed: {
  },
  methods: {
        checkAgeYears(fieldName) {
      switch (fieldName) {
        case 'FIRST_VALUE_YEARS':
          if(Number(this.firstValueYears) < 0) {
            this.firstValueYears = ''
          }
          break;
        case 'SECOND_VALUE_YEARS':
          if(Number(this.secondValueYears) < 0) {
            this.secondValueYears = ''
          }
          break;
        default:
          break;
      }
    },
    checkAgeMonths(fieldName) {
      switch (fieldName) {
        case 'FIRST_VALUE_MONTHS':
          if(Number(this.firstValueMonths) < 0) {
            this.firstValueMonths = ''
          } else if (Number(this.firstValueMonths) > 11) {
            this.firstValueMonths = ''
          }
          break;
        case 'SECONDT_VALUE_MONTHS':
          if(Number(this.secondValueMonths) < 0) {
            this.secondValueMonths = ''
          } else if (Number(this.secondValueMonths) > 11) {
            this.secondValueMonths = ''
          }
          break;
        default:
          break;
      }
    },
    selectOperator(operator) {
      if(this.operator !== operator) {
        this.yearResult = ''
        this.monthResult = ''
      }
      this.operator = operator
      switch (operator) {
        case 'DIVIDE':
          this.numberValue = true
          this.ageValue = false
          this.divideSelected = 'primary'
          this.multiplySelected = ''
          this.plusSelected = ''
          this.minusSelected = ''
          break;
        case 'MULTIPLY':
          this.numberValue = true
          this.ageValue = false
          this.divideSelected = ''
          this.multiplySelected = 'primary'
          this.plusSelected = ''
          this.minusSelected = ''
          break;
        case 'PLUS':
          this.numberValue = false
          this.ageValue = true
          this.divideSelected = ''
          this.multiplySelected = ''
          this.plusSelected = 'primary'
          this.minusSelected = ''
          break;
        case 'MINUS':
          this.numberValue = false
          this.ageValue = true
          this.divideSelected = ''
          this.multiplySelected = ''
          this.plusSelected = ''
          this.minusSelected = 'primary'
          break;
        default:
          break;
      }
    },
    checkOrderValue () {
      if(!Number.isNaN(this.orderValue) && Number(this.orderValue) < 0) {
        this.orderValue = ''
      }
    },
    calculate() {
      if(this.firstValueYears !== '' && this.firstValueMonths !== '') {
        if(this.operator === 'DIVIDE') {
          if(!Number.isNaN(this.orderValue)) {
            let result = timeMachine.divideAge(timeMachine.age(this.firstValueYears, this.firstValueMonths, 0), this.orderValue)
            this.yearResult = result.years
            this.monthResult = result.months
          }
        } else if(this.operator === 'MULTIPLY') {
          if(!Number.isNaN(this.orderValue)) {
            let result = timeMachine.multiplyAge(timeMachine.age(this.firstValueYears, this.firstValueMonths, 0), this.orderValue)
            this.yearResult = result.years
            this.monthResult = result.months
          }
        } else if (this.operator === 'PLUS') {
          if(!Number.isNaN(this.secondValueYears) && !Number.isNaN(this.secondValueMonths)) {
            let result = timeMachine.ageAddition(timeMachine.age(this.firstValueYears, this.firstValueMonths, 0), timeMachine.age(this.secondValueYears, this.secondValueMonths, 0))
            this.yearResult = result.years
            this.monthResult = result.months
          }
        } else if(this.operator === 'MINUS') {
          if(!Number.isNaN(this.secondValueYears) && !Number.isNaN(this.secondValueMonths)) {
            let result = timeMachine.subtractAge(timeMachine.age(this.firstValueYears, this.firstValueMonths, 0), timeMachine.age(this.secondValueYears, this.secondValueMonths, 0))
            this.yearResult = result.years
            this.monthResult = result.months
          }
        }
      }
    },
  }
}
</script>

<style lang="scss" scoped>
</style>
